import React, { useState } from 'react';
import Dropdown from './Dropdown';
import PropTypes from 'prop-types';

function Section({ onChapterSelect, onSectionSelect }) {
  const chapters = ['', '', '', '',''];
  const sections =  [
    [" ", " ", " ", " ", " ", " "],
    [" ", " ", " ", " ", " ", " ", " "],
    [" CPR&AED()", " ()", " ()", "                 "],
    [" ", " ", " "],
    [" ", " ", " ()"]
  ];

  const [selectedChapter, setSelectedChapter] = useState(null);
  const [selectedSection, setSelectedSection] = useState(null);

  function findElementIndex(matrix, element) {
    for (let i = 0; i < matrix.length; i++) {
      for (let j = 0; j < matrix[i].length; j++) {
        if (matrix[i][j] === element) {
          return j;
        }
      }
    }
    return null;
  }


  const handleChapterSelect = (chapter) => {
    const chapterIndex = chapters.indexOf(chapter);
    setSelectedChapter(chapterIndex);
    setSelectedSection(null);  // 
    onChapterSelect(chapterIndex);  // 
  };

  const handleSectionSelect = (section) => {
    const sectionIndex = findElementIndex(sections, section);
    setSelectedSection(sectionIndex);
    onSectionSelect(sectionIndex);  // 
  };

  return (
    <div className="Section">
      <header className="Section-header">
        <Dropdown title="" items={chapters} onSelect={handleChapterSelect} /> <br/>
        {selectedChapter !== null && (
          <Dropdown title="" items={sections[selectedChapter]} onSelect={handleSectionSelect} />
        )}
      </header>
    </div>
  );
}

Section.propTypes = {
  onChapterSelect: PropTypes.func.isRequired,
  onSectionSelect: PropTypes.func.isRequired,
};

export default Section;
